import React from 'react';
import styled from 'styled-components'
import HangmanDrawingSection from '../InGamePage/DrawingAndInfoSection/HangmanDrawing/HangmanDrawing';



const StartPageWrapper = styled.div`
 height: 100%;
 width: 100%;

display: flex;
flex-flow: row wrap;
`

const StartGameButton = styled.button`
position: absolute;
top: 80%;
left: 50%;
transform:translateX(-50%);
height: 3rem;
width: 10rem;
border-radius: 100px;
font-size: 1.2rem;
letter-spacing: 3px;
background-color: ${({ theme }) => theme.colors.black};
color: ${({ theme }) => theme.colors.gray};

`



const startPage = ({ isGameStarted, startGame }) => (

    <StartPageWrapper
        isGameStarted={isGameStarted}>

        <HangmanDrawingSection />

        <StartGameButton
            onClick={startGame}>Start Game</StartGameButton>
    </StartPageWrapper>
)

export default startPage;