import styled from 'styled-components';

const StylesForAllParts = styled.div`
    position: absolute;
    background-color: ${({ theme }) => theme.colors.white};
    transition: .8s;
    &::after,&::before{
        background-color: ${({ theme }) => theme.colors.white};
    }
`

export const GallowsBasis = styled(StylesForAllParts)`
    top: 95%;
    height: .3rem;
    left: 5%;
    width: 70%;
`

export const GallowBranch = styled(StylesForAllParts)`
    top: 5%;
    bottom: 5%;
    left: 5%;
    width: .4rem;
    &::after{
        top: 0%;
        height: 4rem;
        left: 300%;
        width: 100%;
        transform: rotate(35deg);
    }
`

export const GallowTopBranch = styled(StylesForAllParts)`
    top: 5%;
    height: .4rem;
    left: 5%;
    right: 50%;
`

export const GallowsNoose = styled(StylesForAllParts)`
    top: 5%;
    bottom: ${({ isGameStarted }) => isGameStarted ? '82.5%' : '70%'};
    left: 50%;
    width: .4rem;
    transform: translateX(-50%);
    opacity: ${({ mistakesLeft, isGameStarted }) => mistakesLeft <= 6 || !isGameStarted ? '1' : '0'};
`

export const Head = styled(StylesForAllParts)`
    top: 17.5%;
    height: 3.5rem;
    left: 50%;
    width: 3.5rem;
    transform: translate(-50%);
    opacity: ${({ mistakesLeft, isGameStarted }) => mistakesLeft <= 5 || !isGameStarted ? '1' : '0'};
    border: .1rem solid white;
    border-radius: 50%;

    background-color: transparent;
    ::after,::before{
        top: 20%;
        height: 10%;
        width: 10%;
        border-radius: 50%;
    }
    ::after{
        left: 25%;
    
    }
    ::before{
        right: 25%;
    }
`

export const Body = styled(StylesForAllParts)`
    top: calc(17.5% + 3.5rem);
    height: 5rem;
    left: 50%;
    width: .3rem;
    transform: translateX(-50%);
    opacity: ${({ mistakesLeft, isGameStarted }) => mistakesLeft <= 4 || !isGameStarted ? '1' : '0'};
`

const StylesForArms = styled(StylesForAllParts)`
   top: calc(17.5% + 3.5rem);
   height: .2rem;
   width: 3rem;
   border-radius: 30%;
`


const StylesForLegs = styled(StylesForAllParts)`
    top: calc(17.5% + 3.5rem + 4.8rem);
    width: 4rem;
    height: .2rem;
    border-radius: 30%;

`

export const LeftArm = styled(StylesForArms)`
    left: 50%;
    transform: translateX(-100%) rotate(-30deg);
    transform-origin: top right;
    opacity: ${({ mistakesLeft, isGameStarted }) => mistakesLeft <= 3 || !isGameStarted ? '1' : '0'};

`

export const RightArm = styled(StylesForArms)`
    right: 50%;
    transform: translateX(100%) rotate(30deg);
    transform-origin: top left;
    opacity: ${({ mistakesLeft, isGameStarted }) => mistakesLeft <= 2 || !isGameStarted ? '1' : '0'};
`

export const LeftLeg = styled(StylesForLegs)`
    left: 50%;
    transform: translateX(-100%) rotate(-50deg);
    transform-origin: top right;
    opacity: ${({ mistakesLeft, isGameStarted }) => mistakesLeft <= 1 || !isGameStarted ? '1' : '0'};

`

export const RightLeg = styled(StylesForLegs)`
    right: 50%;
    transform: translateX(100%) rotate(50deg);
    transform-origin: top left;
    opacity: ${({ mistakesLeft, isGameStarted }) => mistakesLeft <= 0 || !isGameStarted ? '1' : '0'};

`